import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import About from '@/components/About'
import Contact from '@/components/Contact'
import New from '@/components/New'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      icon: 'home'
    },
    {
      path: '/about',
      name: 'About',
      component: About,
      icon: 'info-circle'
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact,
      icon: 'envelope'
    },
    {
      path: '/new',
      name: 'New',
      component: New,
      icon: 'birthday-cake'
    }
  ]
})



