<template>
  <div id="app">
    <el-menu :router="true" :default-active="$route.path" theme="dark" mode="horizontal">
      <template v-for="rule in $router.options.routes">
        <el-menu-item :index="rule.path">
          <i :class="'fa fa-' + rule.icon" aria-hidden="true"></i> {{ rule.name }}
        </el-menu-item>
      </template>
    </el-menu>
    <div id="content">
      <el-alert :title="ENV" type="info" show-icon></el-alert>
       <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      'ENV': process.env.NODE_ENV
    }
  },
  methods: {
    navSelect: function (index, indexPage) {
      console.log(index, indexPage)
    }
  },
  mounted () {
    document.title += ` ${process.env.NODE_ENV}`
  }
}
</script>

<style lang="scss">
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>



